<script setup >
import { ref, computed, onMounted } from 'vue'
import { list as questions, remove, choiceAdd } from '@/api/hmmm/questions'
import { simple as subjects } from '@/api/hmmm/subjects'
import { simple as directorys } from '@/api/hmmm/directorys'
import { simple as tags } from '@/api/hmmm/tags'
import { simple as users } from '@/api/base/users'
import { dayjs, ElMessage, ElMessageBox } from 'element-plus'
import { Check, Delete, Edit } from '@element-plus/icons-vue'
import { html2Text } from '@/utils'
import { provinces, citys, direction } from '@/api/hmmm/citys'
import { useRouter } from 'vue-router'
const router = useRouter()
// 表单数据
const form = ref([{
  subjectID: null,
  catalogID: null,
  city: '',
  direction: '',
  questionType: null,
  difficulty: '',
  remarks: '',
  tags: null,
  creatorID: null,
  shortName: '',
  province: ''
}])
// 试题数据
const list = ref([])
// 请求页数
const pageDate = ref({
  page: 1,
  pagesize: 5
})
// 总页数
const total = ref()
// 渲染页面
const getList = async () => {
  const res = await questions(pageDate.value)
  list.value = res.data.items
  total.value = res.data.counts
}
// 学科详情列表
const simpleSubject = ref([])
// 录入人
const usersInfo = ref()
onMounted(async () => {
  const data = await users()
  const { data: subjectsData } = await subjects()
  simpleSubject.value = subjectsData
  usersInfo.value = data.data

  getList()
})
// 题型
const Types = [
  { num: 1, label: '单选', value: '简单' },
  { num: 2, label: '多选', value: '一般' },
  { num: 3, label: '简答', value: '困难' }
]
// 计算更新页面数据
const tableData = computed(() => {
  list.value.forEach((item) => {
    item.subjectID = simpleSubject.value.find((i) => i.value === item.subjectID).label
    item.questionType = Types.find((i) => i.num === Number(item.questionType) || i.label === item.questionType).label // 转换成数字进行比较,防止后端参数问题
    item.difficulty = Types.find((i) => i.num === item.difficulty * 1 || i.value === item.difficulty).value // 转换成数字进行比较
    item.addDate = dayjs(item.addDate).format('YYYY-MM-DD HH:mm:ss')
    item.question = html2Text(item.question)
  })
  return list.value
})
// 页码改变
const currentChange = (val) => {
  pageDate.value.page = val
  getList()
}
// 总条数改变
const sizeChange = (val) => {
  pageDate.value.pagesize = val
}
// 筛选
const simpleDirectory = ref()
const simpleTags = ref()
// 选择学科，目录，标签
const change = async (val) => {
  const { data } = await directorys({
    subjectID: val
  })
  const res = await tags({
    subjectID: val
  })
  simpleDirectory.value = data
  simpleTags.value = res.data
}
// 选择省份,选择地区
const provincesDate = provinces()
const cityDate = ref([])
const changeProvinces = (val) => {
  cityDate.value = citys(val)
  console.log(cityDate)
}
// 删除
const deleteDate = async (id) => {
  await ElMessageBox.confirm(
    'proxy will permanently delete the file. Continue?',
    'Warning',
    {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }
  )
  try {
    await remove(id)
    getList()
    ElMessage({
      type: 'success',
      message: 'Delete completed'
    })
  } catch (error) {
    ElMessage({
      type: 'info',
      message: 'Delete canceled'
    })
  }
}
// 编辑
const editDate = (id) => {
  router.push({
    path: '/questions/new',
    query: {
      id
    }
  })
}
// 预览
const previewDate = () => {

}
// 精选
const perfectDate = async (id) => {
  await ElMessageBox.confirm(
    'proxy will permanently delete the file. Continue?',
    'Warning',
    {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }
  )
  try {
    await choiceAdd({
      id,
      choiceState: 1
    })
    getList()
    ElMessage({
      type: 'success',
      message: 'Delete completed'
    })
  } catch (error) {
    ElMessage({
      type: 'info',
      message: 'Delete canceled'
    })
  }
}
// 新增试题
const addQuestions = () => {
  router.push('/questions/new')
}
// 搜索
const search = () => {

}
// 清除
const clear = () => {

}
</script>

<template>
  <div class="box">
    <el-row justify="end">
      <el-button type="success" size="small" @click="addQuestions">新增试题</el-button>
    </el-row>
<!-- 筛选 -->
    <el-form :model="form" style="margin-top: 10px;" size="small" label-position="right" label-width="80px">
      <!-- 第一行 -->
      <el-row :gutter="20">
        <el-col :span="6"> <el-form-item label="学科">
            <el-select v-model="form.subjectID" placeholder="请选择"   @change=change>
              <el-option
              v-for=" item in simpleSubject"
              :label="item.label"
              :value="item.value"
              />
            </el-select>
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="二级目录">
            <el-select v-model="form.catalogID" placeholder="请选择">
              <el-option
              v-for="item in simpleDirectory"
               :label="item.label"
                :value="item.value"
                />
            </el-select>
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="标签">
            <el-select v-model="form.tags" placeholder="请选择">
              <el-option
              v-for="item in simpleTags"
               :label="item.label"
                :value="item.value"
                />
            </el-select>
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="关键字">
          <el-input v-model="form.keyword" />
          </el-form-item></el-col>
      </el-row>
      <!-- 第二行 -->
      <el-row :gutter="10">
        <el-col :span="6"> <el-form-item label="试题类型">
            <el-select v-model="form.questionType" placeholder="请选择">
              <el-option
              v-for="item in Types"
               :label="item.label"
                :value="item.num"
                />
            </el-select>
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="难度">
            <el-select v-model="form.difficulty" placeholder="请选择">
              <el-option
              v-for="item in Types"
               :label="item.value"
                :value="item.num"
                />
            </el-select>
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="方向">
            <el-select v-model="form.direction" placeholder="请选择">
              <el-option
              v-for="item in direction"
               :label="item.label"
                :value="item.value"
                />
            </el-select>
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="录入人">
            <el-select v-model="form.creatorID" placeholder="请选择">
              <el-option
              v-for="item in usersInfo"
               :label="item.username"
                :value="item.id"
                />
            </el-select>
          </el-form-item></el-col>
      </el-row>
      <!-- 第三行 -->
      <el-row :gutter="20">
        <el-col :span="6"> <el-form-item label="题目备注">
            <el-input v-model="form.remarks" />
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="企业简称">
            <el-input v-model="form.shortName" />
          </el-form-item></el-col>
        <el-col :span="6"> <el-form-item label="城市" style="font-weight: 700;">
            <el-row :gutter="5">
              <el-col :span="12"> <el-select v-model="form.province" placeholder="请选择" @change="changeProvinces">
                <el-option
              v-for="item in provincesDate"
               :label="item"
               :value="item"
                />
                </el-select></el-col>
              <el-col :span="12"> <el-select v-model="form.city" placeholder="请选择">
                <el-option
              v-for="item in cityDate"
               :label="item"
               :value="item"
                />
                </el-select></el-col>
            </el-row>
          </el-form-item></el-col>
        <el-col :span="6"> <el-row justify="end">
            <el-button size="small" @click="clear">清除</el-button>
            <el-button type="primary" @click="search" size="small">搜索</el-button>
          </el-row></el-col>
      </el-row>
    </el-form>
    <!-- 总共数据 -->
    <el-row>
    <el-alert class="text" :closable="false" :title="`数据一共${total}条`" type="info" show-icon />
  </el-row>
  <!-- 表格 -->
  <el-table :data="tableData" header-cell-class-name="tableStyle"  style="width: 100%;margin-top: 10px;">
      <el-table-column prop="number" label="试题编号" width="150px"/>
      <el-table-column prop="subjectID" label="学科" />
      <el-table-column prop="catalog" label="目录" />
      <el-table-column prop="questionType" label="题型" />
      <el-table-column prop="question" label="题干" />
      <el-table-column prop="addDate" label="录入时间" width="200px" />
      <el-table-column prop="difficulty" label="难度" />
      <el-table-column prop="creator" label="录入人" />
      <el-table-column prop="username" label="操作" width="160px">
        <template #default="{row}">
          <el-button size="small" type="primary"  circle  plain @click="previewDate"><el-icon><View /></el-icon></el-button>
          <el-button size="small" type="success" :icon="Edit" circle plain  @click="editDate(row.id)"/>
          <el-button size="small" type="danger" :icon="Delete" circle plain @click="deleteDate(row.id)"/>
          <el-button size="small" type="warning" :icon="Check" circle plain @click="perfectDate(row.id)"/>
        </template>
      </el-table-column>
  </el-table>
  <!-- 分页 -->
  <el-row justify="end" style="margin-top:15px;">
      <el-pagination
        small
        background
        :total=total
        class="mt-4"
        :default-page-size="pageDate.pagesize"
        :page-sizes="[5, 10, 20, 50]"
        layout="prev, pager, next,sizes, jumper"
        @size-change="sizeChange"
        @current-change="currentChange"
      />
  </el-row>
  </div>
</template>

<style scoped >
.box {
  margin: 10px;
  padding: 10px;
  background-color: #fff;

}
.box .el-table>>> .tableStyle{
  background:#fafafa;
      border-bottom: 2px solid #e8e8e8;
}

</style>
